import Vue from 'vue'
import Vuex from 'vuex'
import searchForm from './modules/search-form'
import flightOffers from './modules/flight-offers'
import errors from './modules/errors'
import auth from './modules/auth'
import myFlights from './modules/my-flights'


Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    isLoading: false
  },
  getters:{
    getIsLoading: state => {
      return state.isLoading;
    }
  },
  mutations: {
    setIsLoading: (state,arg) =>{
      state.isLoading = arg;
    }
  },
  actions: {

  },
  modules: {
    errors,
    searchForm,
    flightOffers,
    auth,
    myFlights
    

  }
})
