<template>
  <div class="container h-60 mt-4 fillColor">
    <loading
      v-if="getIsLoading"
      :is-full-page="true"
      :loader="'dots'"
      :active="true"
      :color="'#008ec6'"
      :height="80"
      :width="80"
      :background-color="'#999999'"
    ></loading>
    <div class="d-flex row p-2">
      <div class="form-group is-valid col">
        <label class="font-weight-bold text-white" for="from">
          From
          <span>
            <departure-icon></departure-icon>
          </span>
        </label>
        <auto-complete :fromIsValid="fromIsValid" :fromToValue="from" @valueChange="fromUpdate"></auto-complete>
        <input v-model="from" hidden id="from" name="from" type="text" />
      </div>
      <div class="form-group col">
        <label class="font-weight-bold text-white" for="to">
          To
          <span>
            <arrival-icon></arrival-icon>
          </span>
        </label>
        <auto-complete :toIsValid="toIsValid" :fromToValue="to" @valueChange="toUpdate"></auto-complete>
        <input v-model="to" hidden id="to" name="to" type="text" />
      </div>
    </div>
    <div class="d-flex row p-2">
      <div class="form-group col">
        <label class="font-weight-bold text-white" for="depart">Depart</label>
        <input
          :class="[departValid]"
          name="depart"
          id="depart"
          class="form-control"
          type="date"
          v-model="compDepartDate"
        />
      </div>
      <div class="form-group col">
        <label class="font-weight-bold text-white" for="return">Return</label>
        <input
          :class="[returnValid]"
          name="return"
          id="return"
          class="form-control"
          type="date"
          v-model="compReturnDate"
        />
      </div>
      <div class="form-group col-sm">
        <label class="font-weight-bold text-white" for="passengers">Passengers</label>
        <input
          name="passengers"
          id="passengers"
          class="form-control"
          type="Number"
          min="1"
          v-model="passengers"
        />
      </div>
    </div>
    <div class="d-flex justify-content-center p-2s">
      <div class="form-group">
        <button @click="postSearchForm" class="btn btn-outline-light font-weight-bold btn-lg">Search</button>
      </div>
    </div>
  </div>
</template>

<script>
import AutoComplete from "./autoComplete";
import DepartureIcon from "./svg/Departure";
import ArrivalIcon from "./svg/Arrival";
import Loading from "vue-loading-overlay";
import "vue-loading-overlay/dist/vue-loading.css";
export default {
  components: {
    AutoComplete,
    DepartureIcon,
    ArrivalIcon,
    Loading
  },
  data() {
    return {
      from: "",
      to: "",
      passengers: 1,
      fromIsValid: "",
      toIsValid: "",
      departDate: undefined,
      returnDate: undefined,
      departValid: "",
      returnValid: "",
      isLoading: false
    };
  },
  computed: {
    getIsLoading() {
      return this.isLoading;
    },
    compDepartDate: {
      get() {
        return this.departDate;
      },
      set(expected) {
        const departDate = new Date(expected);
        const today = new Date();
        this.departDate = expected;
        if (departDate > today) this.departValid = "is-valid";
        if (departDate <= today) this.departValid = "is-invalid";
      }
    },
    compReturnDate: {
      get() {
        return this.returnDate;
      },
      set(expected) {
        const returnDate = new Date(expected);
        const today = new Date();
        this.returnDate = expected;
        if (returnDate > today && returnDate > new Date(this.departDate))
          this.returnValid = "is-valid";
        if (returnDate <= today) this.returnValid = "is-invalid";
      }
    },
    getFrom: {
      get() {
        return this.from;
      },
      set(val) {
        this.from = val;
      }
    }
  },
  methods: {
    toastHandling(message, type) {
      this.$toasted.show(message, {
        position: "top-center",
        duration: "5000",
        type: type,
        theme: "outline",
        className: "mt-4"
      });
    },
    setFrom(value) {
      this.from = value;
    },
    toUpdate(value) {
      if (value === "") this.toIsValid = "";
      else if (value !== "" && value.slice(-1) === ")")
        this.toIsValid = "is-valid";
      else this.toIsValid = "is-invalid";
      this.to = value;
    },
    fromUpdate(value) {
      if (value === "") this.fromIsValid = "";
      else if (value !== "" && value.slice(-1) === ")")
        this.fromIsValid = "is-valid";
      else this.fromIsValid = "is-invalid";
      this.from = value;
    },
    async postSearchForm(evt) {
      if (this.fromIsValid === "is-invalid" || this.fromIsValid === "") {
        this.toastHandling("From: field is incorrect", "error");
        return;
      }
      if (this.toIsValid === "is-invalid" || this.toIsValid === "") {
        this.toastHandling("To: field is incorrect", "error");
        return;
      }
      if (
        !this.departDate ||
        !this.returnDate ||
        this.departValid === "is-invalid" ||
        this.returnValid === "is-invalid"
      ) {
        this.toastHandling("Please check the dates", "error");
        return;
      }
      const departCity = this.from.substring(0, this.from.indexOf(" ("));
      const returnCity = this.to.substring(0, this.to.indexOf(" ("));
      const searchResult = {
        from: this.from,
        to: this.to,
        depart: this.departDate,
        return: this.returnDate,
        passengers: this.passengers,
        departCity,
        returnCity
      };
      this.isLoading = true;
      await this.$store
        .dispatch("postFlightSearch", searchResult)
        .then(async () => {
          this.isLoading = false;
          await this.$router.push("Flights");
        });
      this.isLoading = false;
      evt.preventDefault();
    }
  },
  async beforeMount() {},
  beforeCreate() {}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
.fillColor {
  background-color: black;
  opacity: 60%;
  border-radius: 20px;
}
</style>
