const state = {
    errorHandler: ''

}
const getters = {
    getErrorHandler: state => {
        console.log('inside getErrorHandler')
        return state.errorHandler;
      }
}

const actions = {
   
}

const mutations = {
    setErrorHandler: (contex,err) => {
        contex.state.errorHandler = err;
      },
      resetErrorHandler: contex => {
        contex.state.errorHandler = ''
      }

}

export default {
    getters,
    mutations,
    actions,
    state
}