import axios from 'axios'
import Vue from 'vue'
import router from '../../router/index'
const state = {
    user_id: '',
    token: '',
    isAuth: false
}
const getters = {
    getUserId: state => {
        return state.user_id;
    },
    getToken: state => {
        return state.token;
    },
    getIsAuth: state => {
        return state.isAuth;
    }
}

const actions = {
    submitSignUp: async (contex, user) => {
        try {

            let result = await axios({
                url: 'signup',
                method: 'post',
                data: user
            });
            if (result.status === 201 || result.status === 200) {
                return {
                    type: 'success',
                    message: result.data.email + ' was created'
                }
            }
        }
        catch (err) {
            if (err.response.status === 409) {
                return {
                    type: 'error',
                    message: 'The user already exist, please try a different email'
                }
            }
            else{
                return {
                    type: 'error',
                    message: 'There have some problem with the server,please try again later'
                }
            }
        }

    },
    submitLogin: async (contex, userDetails) => {
        try {
            const result = await axios({
                url: 'login',
                method: 'post',
                data: userDetails,
            });
            contex.state.token = result.data.token;
            contex.state.user_id = result.data.userId;
            contex.state.isAuth = true;
            const expiresIn = 60 * 60 *1000;

            setTimeout(() => {
                Vue.toasted.show('You are logging out...', {
                    type: 'error',
                    theme: 'outline',
                    position: 'top-center',
                    duration: 1500
                });
                setTimeout(() => {
                    contex.state.isAuth = false;
                    contex.state.token = '';
                    contex.state.user_id = ';';
                    router.push('/');
                }, 2000)
            }, expiresIn)

            return {
                type: 'success',
                message: 'login successful'
            }

        }
        catch (err) {
            if (err.response.status === 404) {
                return {
                    type: 'error',
                    message: userDetails.email + ' not found please try again or signup'
                }
            }
            if (err.response.status === 403) {
                return {
                    type: 'error',
                    message: userDetails.password + ' is incorrect,please try again'
                }
            }
            else{
                return {
                    type: 'error',
                    message: 'There have some problem with the server,please try again later'
                }
            }
        }

    },
    logoutHandler: contex => {
        contex.state.isAuth = false;
        contex.state.token = '';
        contex.state.user_id = ';'
        Vue.router.push('/');

    },
    resetPassword: async (contex,email) =>{
        console.log(email)
        try{
            const result = await axios({
                url: 'reset',
                method: 'post',
                data: {email}
            })
            if(result.status === 200){
                return {
                    type: 'success',
                    message: 'Please check your email address to reset your password'
                }
            }
        }
        catch(err){
            if (err.response.status === 404) {
                return {
                    type: 'error',
                    message: email + ' not found please try again or signup'
                }
            }
            else{
                return {
                    type: 'error',
                    message: 'There have some problem with the server,please try again later'
                }
            }
        }
    },
    submitNewPassword: async (contex,userDetails) => {
        try{
            const result = await axios({
                url: 'new-password',
                method: 'post',
                data: userDetails
            })
            if(result.status === 200 || result.status === 201){
                return {
                    type: 'success',
                    message: 'Password was changed! you are redirected to login'
                }
            }
        }
        catch(err){
            if(err.response.status === 401){
                return {
                    type: 'error',
                    message: 'User not exist or the token has expire, please try to reset again'
                }
            }
            else{
                return {
                    type: 'error',
                    message: 'Some problem with the server please try again later'
                }
            }
        }
    }

}

const mutations = {


}

export default {
    getters,
    mutations,
    actions,
    state
}