import axios from 'axios'

const state = {
    myFlights: [],

}
const getters = {
    getMyFlights: state => {
        return state.myFlights;
    }
}

const actions = {
    addFlight: async (contex, arr) => {

        try {
            const result = await axios({
                url: 'add-flight',
                method: 'post',
                headers: {
                    Authorization: 'Bearer ' + contex.rootState.auth.token
                },
                data: {
                    flight: arr[0],departCity: arr[1],returnCity:arr[2]
                }

            })
            if(result.status === 200 || result.status === 201){
                return {
                    type: 'success',
                    message: 'Flight was added to favorites!'
                }
            }
        }
        catch (err) {
            if(err.response.status === 401){
                return {
                    type: 'error',
                    message: 'No Authorization'
                }
            }
            if(err.response.status === 403){
                return {
                    type: 'error',
                    message: 'This flight is already exist'
                }
            }
          
        }
    },
    deleteFlights: async (contex,arg) => {
        
        try{
            const result = await axios({
                url: 'delete-flight',
                method: 'delete',
                headers: {
                    Authorization: 'Bearer ' + contex.rootState.auth.token,
                },
                data: {
                    flight: arg
                }
            })
            await contex.dispatch('fetchMyFlights');
            if(result.status === 200 || result.status === 201 || result.status === 204){
                return {
                    type: 'success',
                    message: 'Flight was deleted successfuly!'
                }
            }

        }
        catch(err){
            return {
                type: 'error',
                message: 'Some problem with the server please try again later'
            }
        }
    },
    fetchMyFlights: async contex => {
            console.log('test')
            try{
                const result = await axios({
                    url: 'flights',
                    method: 'get',
                    headers: {
                        Authorization: 'Bearer ' + contex.rootState.auth.token
                    }
                })
                contex.state.myFlights = result.data.data;
     
                
            }
            catch(err){
                if(err.response.status === 401){
                    return {
                        type: 'error',
                        message: 'No Authorization'
                    }
                }
            }
    }
}

const mutations = {


}

export default {
    getters,
    mutations,
    actions,
    state
}