<template>
  <div class="bodyImg">
    <zoom-x-transition :duration="1500">
      <div v-show="getShow">
        <search-form></search-form>
      </div>
    </zoom-x-transition>
    <div class="container p-4 w-75">
      <loading
        v-if="getIsLoading"
        :is-full-page="false"
        :loader="'dots'"
        :active="true"
        :color="'#008ec6'"
        :height="80"
        :width="80"
        :background-color="'#999999'"
      ></loading>
      <zoom-y-transition :duration="1500">
        <vueper-slides
          v-show="showFlightsOffers"
          slide-image-inside
          class="no-shadow"
          :autoplay="true"
          :duration="3000"
          :visible-slides="3"
          slide-multiple
          :gap="3"
          :slide-ratio="1 / 4"
          :dragging-distance="200"
          :breakpoints="{ 800: { visibleSlides: 2, slideMultiple: 2 } }"
        >
          <vueper-slide
            v-for="(i,index) in flightsOffers"
            :key="index"
            :title="i.toString()"
            :image="img"
            :style="`border-radius: 20px`"
          >
            <template v-slot:content>
              <div>
                <div
                  class="vueperslides-font mt-2 text-uppercase font-italic text-center font-weight-bold d-flex flex-column justify-content-center"
                >
                  <div class="dates">{{getDepartCity}} - {{i.returnCity}}</div>
                  <div
                    class="dates"
                  >{{i.depart.departure.fullDateTime.substring(0,10)}} - {{i.return.departure.fullDateTime.substring(0,10)}}</div>
                  <div class="price">{{i.price.total + i.price.symbol}}</div>
                  <button
                    @click="addFlight(i,getDepartCity,i.returnCity)"
                    
                    type="button"
                    class="btn btn-dark btn-sm font-weight-bold opacity dates"
                  >Add</button>
                </div>
              </div>
            </template>
          </vueper-slide>
        </vueper-slides>
      </zoom-y-transition>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src

import SearchForm from "../components/SearchForm";
import { ZoomXTransition, ZoomYTransition } from "vue2-transitions";
import { VueperSlides, VueperSlide } from "vueperslides";
import "vueperslides/dist/vueperslides.css";
import Loading from "vue-loading-overlay";
import "vue-loading-overlay/dist/vue-loading.css";
export default {
  components: {
    SearchForm,
    ZoomXTransition,
    ZoomYTransition,
    VueperSlides,
    VueperSlide,
    Loading
  },
  data() {
    return {
      show: false,
      img: "https://media.timeout.com/images/105434111/630/472/image.jpg",
      showFlightsOffers: false,
      flightsOffers: this.$store.getters.getFlightsOffers,
      departCity: this.$store.getters.getdepartCityOffer,
      isLoading: false
    };
  },
  methods: {
    addFlight(flight, departCity, returnCity) {
      if(!this.getAuth){
        this.toastHandling('Please login or signup first','info')
        return;
      }
      const arr = [flight,departCity,returnCity]
      this.isLoading = true;
      this.$store
        .dispatch("addFlight", arr)
        .then(result => {
          this.isLoading = false;
          this.toastHandling(result.message,result.type)
        });
    },
    toastHandling(message, type) {
      this.$toasted.show(message, {
        position: "top-center",
        duration: "5000",
        type: type,
        theme: "outline",
        className: "mt-4"
      });
    }
  },
  mounted() {
    
    if (this.$store.getters.getLocation) {
      setTimeout(() => {
        this.isLoading = false;
        this.flightsOffers = this.$store.getters.getFlightsOffers;
        this.departCity = this.$store.getters.getdepartCityOffer;
        this.showFlightsOffers = true;
      }, 500);
    } else if (navigator.geolocation) {
      this.$store.commit('locationState',true);
      navigator.geolocation.getCurrentPosition(
        position => {
          const latitude = position.coords.latitude;
          const longitude = position.coords.longitude;
          const location = { latitude, longitude };
          this.isLoading = true;
          this.$store.dispatch("flightOffersLongLat", location).then(() => {
            setTimeout(() => {
              this.isLoading = false;
              this.flightsOffers = this.$store.getters.getFlightsOffers;
              this.departCity = this.$store.getters.getdepartCityOffer;
              this.showFlightsOffers = true;
            }, 500);
          });
        },
        () => {
          this.isLoading = false;
        }
      );
    }
    setTimeout(() => {
      this.show = true;
    }, 500);
  },
  computed: {
    getShow() {
      return this.show;
    },
    getFlightsOffer() {
      return this.flightsOffers;
    },
    getDepartCity() {
      return this.departCity;
    },
    getIsLoading() {
      return this.isLoading;
    },
    getAuth() {
      return this.$store.getters.getIsAuth;
    }
  },
  beforeRouteLeave(to, from, next) {
    // ...
    this.show = false;
    this.showFlightsOffers = false;
    setTimeout(() => {
      next();
    }, 1500);
  }
};
</script>
<style  >
.border2 {
  opacity: 100%;
  border-radius: 20px;
}
.vueperslides-font {
  font-size: 2.2vw;
  opacity: 0.99;
  color: #ffffff;
  text-shadow: 1px 3px 0 #131111, 1px 13px 5px #1d1b1b;
}

.vueperslides__arrow {
  outline: none !important;
}
.opacity {
  opacity: 0.9;
}
.price {
  font-size: 0.8em;
}
.dates {
  font-size: 0.7em;
}
.bodyImg {
  background: url("../assets/images/FBe0oc3.jpg") no-repeat fixed;
  background-size: cover;
  height: calc(100vh - 80px);
  overflow: auto;
}
</style>