<template>
  <div @click="show = false">
    <input
      class="form-control font-weight-bold"
      type="text"
      :class="[toIsValid,fromIsValid]"
      @input="filteredCitiesMet"
      @keyup="onArrow"
      spellcheck="false"
      v-bind:value="getCity"
      v-on:input="getCity= $event.target.value"
    />
    <!--v-$model="getCity"-->
    <ul class="list-group w-75" v-if="filteredCities && show">
      <li
        v-for="(filteredCity,index) in filteredCities"
        class="list-group-item py-1"
        :class="[(activeIndex === index) ? 'font-weight-bold list-group-item list-group-item-action active':'list-group-item list-group-item-action']"
        :key="index"
        @mouseover="setActive(index)"
        @mouseout="deActive()"
        @click="setCity(filteredCity.name + ' (' + filteredCity.code + ')')"
      >{{filteredCity.name + ' (' + filteredCity.code + ')'}}</li>
    </ul>
    <small
      v-if="showNote"
      id="passwordHelpBlock"
      class="form-text text-muted"
    >Example: "Tel Aviv (TLV)"</small>
  </div>
</template>
<script>
export default {
  data() {
    return {
      city: "",
      filteredCities: [],
      show: true,
      activeIndex: 0,
      showNote: true,
      arrowCounter: 0
    };
  },
  props: ["fromIsValid", "toIsValid"],

  methods: {
    handleClickOutside(evt) {
      if (!this.$el.contains(evt.target)) {
        this.show = false;
      }
    },
    onArrow(event) {
      //console.log(event)
      let temp = this.activeIndex;
      if(event.code == "ArrowDown")
      temp = (this.activeIndex+1) % this.filteredCities.length;
      else if(event.code == "ArrowUp"){
        if(this.activeIndex ===0){
          temp =4;
        }
        else{
           temp = (this.activeIndex-1) % this.filteredCities.length;
        }
      }
      else if(event.code === 'Enter'){
          this.setCity(this.filteredCities[this.activeIndex].name + ' (' + this.filteredCities[this.activeIndex].code + ')');
          return;
      }
      
      this.setActive(temp);
      //console.log(this.activeIndex)
    },
    
    filteredCitiesMet() {
      this.show = true;
      let index = 0;
      this.filteredCities = this.$store.getters.getCities.filter(
        filteredCity => {
          if (
            filteredCity.name
              .toLowerCase()
              .startsWith(this.city.toLowerCase()) &&
            index < 5
          ) {
            index++;

            return filteredCity.name;
          }
        }
      );
    },
    setCity(city) {
      this.$emit("valueChange", city);

      this.city = city;
      this.show = false;
    },
    setActive(index) {
      this.activeIndex = index;
    },
    deActive() {
      this.activeIndex = -1;
    }
  },
  computed: {
    getShow: {
      get() {
        //return this.$store.getters.getAutoCompleteShow;
        return this.show;
      },
      set(arg) {
        this.show = arg;
      }
    },
    getCity: {
      get() {
        return this.city;
      },
      set(val) {
        this.$emit("valueChange", val);
        this.city = val;
      },
      compShowNote: {
        get() {
          return this.showNote;
        },
        set() {}
      },
      getFil(){
        return this.filteredCities;
      }
    }
  },

  async mounted() {
    document.addEventListener("click", this.handleClickOutside);
  },
  destroyed() {
    document.removeEventListener("click", this.handleClickOutside);
  },
  async created() {}
};
</script>
<style lang="scss" scoped>
.border {
  border-radius: 10px;
}
input {
  outline-style: none;
}
li {
  cursor: pointer;
}
ul {
  position: absolute;
  z-index: 999;
}
</style>