import axios from 'axios'

const state = {
    cities: [],
    errorStatus: '',
    flights: [],
    departCity: '',
    returnCity: ''
}
const getters = {
    getCities: state => {
        return state.cities;
    },
    getErrorStatus: state => {
        return state.errorStatus
    },
    getFlights: state => {
        return state.flights;
    },
    getDepartCity: state => {
        return state.departCity;
    },
    getReturnCity: state => {
        return state.returnCity;
    }
}

const actions = {
    fetchCities: (contex) => {
        axios.get('cities')
            .then(result => {
                return JSON.parse(JSON.stringify(result.data))

            })
            .then(res => {
                contex.state.cities = JSON.parse(res);
            })
            .catch(err => {
                contex.state.errorStatus = err;
            })

    },
    postFlightSearch: async (contex, arg) => {
        try {
            let response = await axios({
                method: 'post',
                url: 'flights',
                data: arg,
            })
            console.log(response.data)
            contex.state.departCity = await JSON.parse(JSON.stringify(response.data.data.departCity))
            contex.state.returnCity = await JSON.parse(JSON.stringify(response.data.data.returnCity))
            contex.state.flights = await JSON.parse(JSON.stringify(response.data.data.flights))
            
            if (response.status === 200) {
                return {
                    type: 'success',
                    message: 'Flight was added to favorites!'
                }
            }

        }
        catch (err) {
            console.log('err: '+ err)
            if (err.response.status === 503) {
                return {
                    type: 'error',
                    message: 'The server is not ready to handle the request.'
                }

            }
            else {
                return {
                    type: 'error',
                    message: 'Some problem with the server, please try again later.'
                }
            }


        }
    },
    setDepartCity: (state, payload) => {
        state.departCity = payload;
    },
    setReturnCity: (state, payload) => {
        state.returnCity = payload;
    }


}

const mutations = {


}

export default {
    getters,
    mutations,
    actions,
    state
}