import axios from 'axios'

const state = {
    flightOffers: [],
    departCityOffer: '',
    errorStatus: '',
    location: false
}
const getters = {
    getFlightsOffers: state => {
        return state.flightOffers;
    },
    getdepartCityOffer: state => {
        return state.departCityOffer
    },
    getLocation: state => {
        return state.location;
    }
}

const actions = {
    flightOffersLongLat: async (contex, payload) => {
        try {
            console.log(2)
            let response = await axios({
                method: 'post',
                url: 'flights-offers',
                data: payload,
            })
            contex.state.flightOffers = await  JSON.parse(JSON.stringify(response.data.data.flightsOffers));
            contex.state.departCityOffer = await  JSON.parse(JSON.stringify(response.data.data.departCity));
        }
        catch (err) {
            console.log(err)
        }

    }
}

const mutations = {
    locationState: (state,arg) => {
        state.location = arg;
    }

}

export default {
    getters,
    mutations,
    actions,
    state
}